.container {
    min-width: 320px;
    padding: 20px;
    background: #fff3e8;
    border: solid 10px #4a1715;
    margin-bottom: 20px;
    border-radius: 25px;
    height: 100px;
    font-size: 26px;
}

.button{
    background:#fff;
    color:#4a1715;
    margin-bottom: 20px;
